                    </div>
                    <!-- 运输要求模块 -->
                    <div class="transport-requiry public-detail" v-else v-for="(list, index) of pathData" :key="index" v-show="index === pathNum">
                        <div class="normal-info-table two-cell">
                            <div class="table-box">
