                            </ul>
                            
                            <Divider />
                            <div class="public-table-content">
                                <div class="l-content">
                                    <!-- 运输服务 -->
                                    <div class="table-data" v-for="(list, index) of pathList.roadList" :key="index" v-show="pathList.lineNum == index || (!pathList.lineNum && index === 0)">
