                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                    <!-- 运输要求模块 -->
                    <div class="transport-requiry public-detail" v-else v-for="(list, index) of saleData" :key="index" v-show="index === pathNum">
                        <div class="normal-info-table two-cell">
                            <div class="table-box">
                                <div class="table">
                                    <div class="cell">
                                        <div class="cell-name">主要运输方式：</div>
                                        <div class="cell-content">{{list.mainTrspMode}}</div>
